<template>
  <div>
    <van-nav-bar left-text="返回" left-arrow @click-left="back" />
    <van-tabs v-model="active" @click="go">
      <van-tab title="早餐"></van-tab>
      <van-tab title="午餐"></van-tab>
      <van-tab title="晚餐"></van-tab>
    </van-tabs>
    <div>
      <router-view />
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import { Toast } from "vant";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      active: 0
    };
  },
  created() {
    this.active = this.foodActive;
    switch (this.active) {
      case 0:
        this.$router.push({ name: "zao" });
        break;
      case 1:
        this.$router.push({ name: "zhong" });
        break;
      case 2:
        this.$router.push({ name: "wan" });
        break;
    }
    let x = location.hash.split("/")[2];

    switch (x) {
      case "Zao":
        this.active = 0;
        break;
      case "Zhong":
        this.active = 1;
        break;
      case "Wan":
        this.active = 2;
        break;
    }
  },
  computed: {
    ...mapState(["foodActive"])
  },
  methods: {
    ...mapMutations(["FoodActive"]),
    back() {
      this.$router.push({ name: "homePage" });
    },
    go() {
      switch (this.active) {
        case 0:
          this.$router.push({ name: "zao" });
          break;
        case 1:
          this.$router.push({ name: "zhong" });
          break;
        case 2:
          this.$router.push({ name: "wan" });
          break;
      }
      this.FoodActive(this.active);
    }
  }
};
</script>

<style lang="less" scoped>
.list {
  margin-top: 30px;
  text-align: center;
  img {
    border-radius: 10px;
  }
  div {
    width: 90%;
    margin: 20px auto;
    font-size: 16px;
    font-weight: bold;
    color: #666;
    border-bottom: 1px solid #999;
  }
}
/deep/ .van-tabs__line{
  background: #1989fa;
}
</style>